nav {
    background-color: var(--green);
}

body {
    min-width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
}

main {
    flex-grow: 1;
    flex-basis: 0;
    overflow-y: auto;
    background-color: var(--green);
    padding-left: var(--x-padding);
    padding-right: var(--x-padding);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
}

header h1 {
    font-size: clamp(2rem, 7vw, 3rem);
    margin-top: 1em;
}

p {
    font-size: clamp(1.2rem, 3vw, 1.2rem);
    margin-top: 24px; 
    /* TODO make responsie */
}

.footer p {
    font-size: clamp(1.2rem, 5vw, 2rem);
    font-weight: 600;
}

ul {
    margin-bottom: -1em;
}

li {
    color: white;
    font-family: "Work Sans", sans-serif;
    font-optical-sizing: auto;
    font-size: clamp(1.2rem, 3vw, 1.2rem);
    font-weight: 400;
    margin-bottom: 12px;
    font-style: normal;
}

.stripes {
    margin-top: 24px;
    margin-bottom: 24px;
    height: 72px;
    background-color: white;

    background: url(./stripes.svg);

    /*
      This is a little convoluted: The stipes are located as <div> elements
      inside each <section> tag. The section tags have padding (--x-padding)
      but we want the stripes to run the full width of the section and ignore
      the padding. Bellow is how that is accomplished..
    */
    width: 100vw;
    margin-left: calc(0px - var(--x-padding));
}